import React, { Component } from 'react';
import classNames from 'classnames';
import VisibilitySensor from 'react-visibility-sensor';
import IconArrowLeft from '../assets/svg/arrow.svg';
import IconArrowRight from '../assets/svg/arrow.svg';
import IconPost from '../assets/svg/post__arrow.svg';

import { useStaticQuery, graphql, Link } from 'gatsby';
import Slider from 'react-slick';
import Img from 'gatsby-image';

export const News = ({ pageContext }) => {
    const data = useStaticQuery(graphql`
    query AllArticlesQuery {
      allStrapiArticle(filter: { Published: { eq: true } }) {
        nodes {
          lang {
            id
            Title
            Short_description
            lang
            Preview_image {
              childImageSharp {
                fluid(
                  fit: COVER
                  cropFocus: CENTER
                ) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            slug
          }
          id
          Title
          Published
          strapiId
        }
      }
    }
  `);

    const sliderElements = [
        ...data.allStrapiArticle.nodes,
        ...data.allStrapiArticle.nodes,
        ...data.allStrapiArticle.nodes
    ];

    function PrevArrow(props) {
        const { className, onClick } = props;

        return (
            <button className={className + ' outline-none'} onClick={onClick}>
                <IconArrowRight
                // className={className}
                // onClick={onClick}
                />
            </button>
        );
    };

    function NextArrow(props) {
        const { className, onClick } = props;

        return (
            <button className={className + ' outline-none'} onClick={onClick}>
                <IconArrowLeft
                // className={className}
                // onClick={onClick}
                />
            </button>
        );
    };

    let state = {
        slideIndex: 0,
        updateCount: 0
    };

    const settings = {
        infinite: true,
        focusOnSelect: false,
        arrows: true,
        dots: true,
        slidesToShow: 2,
        slidesToScroll: 1,
        focusOnSelect: true,
        // autoplay: true,
        speed: 400,
        autoplaySpeed: 4000,
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />,
        afterChange: () =>
            this.setState(state => ({ updateCount: state.updateCount + 1 })),
        beforeChange: (current, next) => this.setState({ slideIndex: next }),
        // beforeChange: (current, next) => {
        //     console.log(current, next);
        // },
        responsive: [
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                },
            },
        ],
    };

    return (
        <div className="news pb-40">
            <div className="row">
                <div className="news__heading pt-24">
                    <VisibilitySensor partialVisibility>
                        {({ isVisible: isVisibleScreen }) => (
                            <h2
                                className={classNames('headline visibility-animation', {
                                    visible: isVisibleScreen,
                                })}
                            >
                                <span>{pageContext.menu_news || 'Latest News'}</span>
                            </h2>
                        )}
                    </VisibilitySensor>
                </div>

                <div className="news news--slider relative pb-4">
                    <Slider {...settings}>
                        {sliderElements.map((article) => (
                            <a
                                key={article.id}
                                href={`/newsroom/${pageContext.lang === 'en' ? '' : `${pageContext.lang}/`
                                    }${article.lang[0].slug}`}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="news__article slider"
                            >
                                <div className="slider__image">
                                    {article.lang && (
                                        <Img
                                            style={{ height: '100%' }}
                                            fluid={
                                                article.lang[0].Preview_image.childImageSharp.fluid
                                            }
                                        />
                                    )}
                                </div>
                                <div className="content py-16 px-11 h-full">
                                    <h3 className="slider__title">{article.Title}</h3>
                                    <p className="slider__description">
                                        {article.lang && article.lang[0].Short_description}
                                    </p>
                                    <div className="inline-flex justify-end w-full">
                                        <button
                                            className="slider__btn inline-flex items-start py-4 px-6 uppercase"
                                        >
                                            Read more{[<IconPost className="ml-4" />]}

                                        </button>
                                    </div>
                                </div>
                            </a>
                        ))}
                    </Slider>
                    <div className="inline-flex mt-10 w-full items-center justify-between navigation">
                        <div className="flex">
                            <span>
                                <input
                                    onChange={e => this.slider.News(e.target.value)}
                                    value={this.state.slideIndex}
                                    type="range"
                                    min={0}
                                />
                                {+ 1}
                            </span>
                            {" / "}
                            <span>
                                {sliderElements.length}
                            </span>
                        </div>
                        <div className="flex">
                            <PrevArrow className="text-gray-250 mr-6" />
                            <NextArrow className="text-orange-600 transform rotate-180" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
